*,
::before,
::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
@font-face {
  font-family: "oleo bold";
  src: url("./font-family/Oleo_Script/OleoScript-Bold.ttf");
}
@font-face {
  font-family: "oleo regular";
  src: url("./font-family/Oleo_Script/OleoScript-Regular.ttf");
}
@font-face {
  font-family: "anton";
  src: url("./font-family/Anton-Regular.ttf");
}
@font-face {
  font-family: "roboto";
  src: url("./font-family/RobotoCondensed-Bold.ttf");
}
html {
  font-size: 62.5%;
}
body {
  font-family: Arial;
  font-size: 1.6rem;
}
.appContainer {
  display: grid;
  grid-template-columns: 1fr 2fr;
  max-width: 100%;
  min-height: 100vh;
}

/*! GAME RULES */
.gameRules {
  grid-area: 1/1/2/2;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background: #f3b98e;
  border-right: 0.3rem solid #f15930;
  padding: 2rem 1rem;
}
.logoDiv {
  font-family: "anton";
  font-weight: 400;
  font-size: 7.5rem;
  min-height: 20vh;
  width: 100%;
  text-align: center;
  line-height: 1;
}
.letterM {
  color: #f15930;
}
.letterB {
  color: #2858a1;
}
.subtitle {
  font-size: 3rem;
  font-family: "roboto";
  font-weight: 800;
  text-align: center;
}
.ref {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 1.8rem;
  text-align: center;
  min-height: 25vh;
  width: 100%;
  gap: 2rem;
}
.children {
  width: 60px;
  height: 60px;
}
.players {
  margin-bottom: 2rem !important;
}
.separator {
  background: #2858a1;
  height: 3px;
  width: 70%;
}
.rules {
  text-align: justify;
  width: 100%;
  height: 55vh;
  padding: 1rem;
}
.rules p {
  margin-bottom: 2rem;
}

/*! BOARD */
.boardDiv {
  grid-area: 1/2/2/3;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100%;
  min-height: 100vh;
  background: url("./assets/kidsroom.jpg") no-repeat center/cover;
}
.titleDiv {
  height: 12vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}
h1 {
  text-align: center;
  text-shadow: 0.2rem 0 0 rgb(255, 255, 255), -0.2rem 0 0 rgb(255, 255, 255),
    0 0.2rem 0 rgb(255, 255, 255), 0 -0.2rem 0 rgb(255, 255, 255),
    0.1rem 0.1rem rgb(255, 255, 255), -0.1rem -0.1rem 0 rgb(255, 255, 255),
    -0.1rem -0.1rem 0 rgb(255, 255, 255), -0.1rem 0.1rem 0 rgb(255, 255, 255);
  font-size: 6rem;
  font-family: "oleo bold";
  color: #f15930;
}
.boardContainer {
  height: 88vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}
.board {
  margin-bottom: 2rem;
}
/* Text message and animation */
.message {
  font-family: "oleo regular";
  animation: 2s message ease infinite;
  color: white;
  text-align: center;
  font-size: 3rem;
  line-height: 1;
  margin-bottom: 1rem;
}
@keyframes message {
  0% {
    color: white;
    transform: scale(0);
    opacity: 0;
    text-shadow: 0.2rem 0 0 #f15930, -0.2rem 0 0 #f15930, 0 0.2rem 0 #f15930,
      0 -0.2rem 0 #f15930, 0.1rem 0.1rem #f15930, -0.1rem -0.1rem 0 #f15930,
      -0.1rem -0.1rem 0 #f15930, -0.1rem 0.1rem 0 #f15930;
  }
  25% {
    color: #f15930;
    transform: scale(1.2);
    opacity: 1;
    text-shadow: 0.2rem 0 0 rgb(255, 255, 255), -0.2rem 0 0 rgb(255, 255, 255),
      0 0.2rem 0 rgb(255, 255, 255), 0 -0.2rem 0 rgb(255, 255, 255),
      0.1rem 0.1rem rgb(255, 255, 255), -0.1rem -0.1rem 0 rgb(255, 255, 255),
      -0.1rem -0.1rem 0 rgb(255, 255, 255), -0.1rem 0.1rem 0 rgb(255, 255, 255);
  }
  50% {
    color: white;
    transform: scale(1);
    opacity: 1;
    text-shadow: 0.2rem 0 0 #f15930, -0.2rem 0 0 #f15930, 0 0.2rem 0 #f15930,
      0 -0.2rem 0 #f15930, 0.1rem 0.1rem #f15930, -0.1rem -0.1rem 0 #f15930,
      -0.1rem -0.1rem 0 #f15930, -0.1rem 0.1rem 0 #f15930;
  }
  100% {
    /* animate nothing to add pause at the end of animation */
    transform: scale(1);
    opacity: 1;
    text-shadow: 2px 0 0 #f15930, -2px 0 0 #f15930, 0 2px 0 #f15930,
      0 -2px 0 #f15930, 1px 1px #f15930, -1px -1px 0 #f15930, 1px -1px 0 #f15930,
      -1px 1px 0 #f15930;
  }
}
table {
  border-spacing: 0;
}
.cell {
  width: 90px;
  height: 90px;
  background-color: #2858a1;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  opacity: 0.9;
}
.white,
.red,
.yellow {
  width: 80px;
  height: 80px;
}
.white {
  background-color: white;
  border-radius: 50%;
}
.red {
  background-color: #f15930;
  border-radius: 50%;
  transition: background-color 0.5s;
}
.yellow {
  background-color: #ffd700;
  border-radius: 50%;
  transition: background-color 0.5s;
}
/* Reset Button */
.buttonBox {
  margin-top: 2rem;
}
.button {
  background-color: #f15930;
  color: white;
  min-height: 50px;
  width: 120px;
  font-size: 2rem;
  border-radius: 0.5rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.5s, color 0.5s;
}
.button:hover {
  background-color: orange;
  color: black;
  transition: background-color 0.5s, color 0.5s;
}

/*! SCREEN ORIENTATION MESSAGE */
#turn {
  display: none;
  width: 100vw;
  height: 100vh;
  background: #fff;
  z-index: 100;
}
#turn div {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
}

/*! MEDIA QUERIES */
@media screen and (max-width: 1023px) {
  .appContainer {
    grid-template-columns: 1fr;
  }
  .gameRules,
  .ref,
  .rules {
    height: auto;
  }
  .gameRules {
    border-right: none;
    border-bottom: 0.3rem solid #f15930;
  }
  .boardDiv {
    grid-area: 2/1/3/2;
  }
  .boardDiv {
    padding-bottom: 1rem;
  }
  .boardContainer {
    padding: 0.5rem;
  }
  .cell {
    width: 85px;
    height: 85px;
  }
  .white,
  .red,
  .yellow {
    width: 75px;
    height: 75px;
  }
}

@media screen and (max-width: 500px) {
  .cell {
    width: 80px;
    height: 80px;
  }
  .white,
  .red,
  .yellow {
    width: 70px;
    height: 70px;
  }
}

/* Sreen orientation message */
@media (orientation: landscape) and (max-width: 992px) {
  #turn {
    display: block;
  }
  .appContainer {
    display: none;
  }
}
